import axios from "axios";
import { useState } from "react";
import Table from "../library/components/Table/Table.js";
const TrackApplication = ({ searchedTrackData, vernacularLanguage, KeyLanguage }) => {

  const [state, setStates] = useState("");
  const [ac, setAcs] = useState("");

  if (searchedTrackData != "LD" && searchedTrackData != "") {
    axios
      .get(
        process.env.REACT_APP_COMMON_SERVICES_BASE_URL + "api/v1/common/states"
      )
      .then((response) => {
        setStates(
          response.data?.filter((item) => {
            return item.stateCd === searchedTrackData?.payload[0]?.stateCode;
          })[0].stateName
        );
      });

    axios
      .get(
        process.env.REACT_APP_COMMON_SERVICES_BASE_URL +
        `api/v1/common/acs/${searchedTrackData?.payload[0]?.districtCode}`
      )
      .then((response) => {
        setAcs(
          response.data?.filter((item) => {
            return item.asmblyNo == searchedTrackData?.payload[0]?.acNo;
          })[0].asmblyName
        );
      });

    let item = [
      {
        id: "p01",
        items: [],
        stat: [
          "step completed",
          "step",
          "step",
          "step completed",
          "step completed",
          "step",
          "step",
        ],
        status: [],
      },
    ];
    var homePageData = item;

    homePageData["0"]?.items?.push(
      "1",
      searchedTrackData?.payload[0]?.formRefNo,
      state,
      ac,
      searchedTrackData?.payload[0]?.firstName,
      searchedTrackData?.payload[0]?.lastName
        ? searchedTrackData?.payload[0]?.lastName
        : "",
      searchedTrackData?.payload[0]?.formType,
      searchedTrackData?.payload[0]?.submissionDate
        ? searchedTrackData?.payload[0]?.submissionDate
        : "",
      searchedTrackData?.payload[0]?.currentStatus
    );
    homePageData["0"]?.status?.push({
      submitted: searchedTrackData?.payload[0]?.submitted,
      bloAssigned: searchedTrackData?.payload[0]?.bloAssigned,
      fvrSubmitted: searchedTrackData?.payload[0]?.fvrSubmitted,
      hearingScheduled: searchedTrackData?.payload[0]?.hearingScheduled,
      fvrSubmittedHearingScheduled:
        searchedTrackData?.payload[0]?.fvrSubmittedHearingScheduled,
      accepted: searchedTrackData?.payload[0]?.accepted,
      rejected: searchedTrackData?.payload[0]?.rejected,
      epicNo: searchedTrackData?.payload[0]?.epicNo,
      formType: searchedTrackData?.payload[0]?.formType,
    });

    return (
      <>
        <div className="row" style={{ marginLeft: 20, marginRight: 20 }} id="style-2">
          {
            <Table
              theadData={[
                "S. No.",
                "Reference No.",
                "State",
                "AC",
                "First Name",
                "Last Name",
                "Form Type",
                "Submission Date",
                "Current Status",
                "",
              ]}
              tbodyData={homePageData}
              className="generic table-responsive"
              category="appHistory"
            />
          }
        </div>
      </>
    );
  }
  else {
    return (
      <div className='result-main' id="style-2">

        <h4 align="center" style={{ marginTop: 10, marginBottom: 10 }}>No Form Tracking Records Found</h4>
      </div>
    )
  }
};

export default TrackApplication;
