import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { validateInput } from "../../utilities/Validator";
import "../../css/common.css";
const TextFieldRegional = ({
  value,
  id,
  borderColor,
  label,
  labelHindi,
  labelEnglish,
  placeholder,
  name,
  required,
  validators,
  type,
  onChange,
  labelClass,
  inputClass,
  labelStyle,
  style,
  disabled,
  spanTitle,
  onKeyPress,
  tabIndex,
  maxLength,
  onKeyUp,
  iconclass,
  divStyle,
  getError,
  minLength,
  Error,
  onBlur,
  refValue,
  onFocus,
  onWheel,
  max,
  min,
  margin,
  lang,
  errorStatus,
  errorLabels,
  errorLabel,
  setErrorStatus
}) => {
  const [error, setError] = useState(false);
  const [success, setSuccess] = useState(false);
  useEffect(() => { }, []);
  const handleChange = (event) => {
    const { value } = event.target;
    if (validateInput(validators, value))
      setError(validateInput(validators, value));
    else if (
      type === "email" &&
      !validateInput(validators, value) &&
      value !== ""
    ) {
      setSuccess(true);
      setError(false);
    } else {
      setError(false);
    }
    onChange(value);
    getError &&
      getError(
        validateInput(validators, value)
          ? validateInput(validators, value)
          : false
      );
  };
  const onBlurHandler =()=>{
    if(/[`!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/i.test(refValue.current.value)){
      onChange('');
      window.alert("Special Character Such as \n !@#$%^&*()_+\-=\[\]{};':\"\\|,.<>\/?~ \n are not allowed")
      return;
    }
    onChange(refValue.current.value);
    if(refValue.current.value!=''){
      if(errorStatus.hasOwnProperty(errorLabel)){
        setErrorStatus(prev=>({...prev,[errorLabel]:false}))
      }
    }
  }
  return (
    <>
      {label && (
        <div className={labelClass}>
          <label className="input-label" style={labelStyle}>
          <span style={{fontWeight: 500}}>{labelHindi}</span>/<br/><span style={{fontWeight: 700}}>{labelEnglish}</span>
            {required ? <b className={"text-danger"}> *</b> : ""}
          </label>
        </div>
      )}
      <div className={inputClass} >

        <div className="form-group">
          <div style={divStyle}>
            <input
              type={type}
              value={value}
              name={name}
              ref={refValue}
              // className="form-control"
              placeholder={placeholder}
              onChange={handleChange}
              style={style}
              disabled={disabled}
              onKeyPress={onKeyPress}
              tabIndex={tabIndex}
              onKeyUp={onKeyUp}
              onBlur={onBlurHandler}
              onFocus={onFocus}
              pattern={max}
              maxLength={maxLength}
              minLength={minLength}
              iconclass={iconclass}
              id={id}
              min={min}
              onWheel={onWheel}
              className={
                error
                  ? "library-input form-control error"
                  : success
                    ? "library-input form-control success"
                    : "library-input form-control"
              }
            ></input>
            {true &&
              <i
                className="fa-regular fa-keyboard"
                aria-hidden="true"
                style={{ color: '#0076a8',backgroundColor:'white',marginLeft:7,cursor: 'pointer' }}
                onClick={() => {
                  console.log("I am Clicked")
                  if(lang!='NA'){
                    window.openKeyboard(lang);
                  }
                  else{
                    
                    setErrorStatus((prev)=>({...prev,keyboard: true}))
                    document.getElementById('KeyboardID').scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" });
                    
                  }
                  
                }}

              ></i>
            }
          </div>
          {spanTitle && <small>{spanTitle}</small>}
          {errorLabel && errorStatus[errorLabel] && <span className={`text-danger errormsg`}>* {errorLabels[errorLabel]}</span>}</div>
      </div>
    </>
  );
};

TextFieldRegional.prototype = {
  value: PropTypes.string,
  label: PropTypes.string,
  placeholder: PropTypes.string,
  validators: PropTypes.array,
  type: PropTypes.string,
  onchange: PropTypes.func.isRequired,
  labelClass: PropTypes.string,
  inputClass: PropTypes.string,
  style: PropTypes.object,
  labelStyle: PropTypes.object,
  divStyle: PropTypes.object,
  iconclass: PropTypes.string,
  maxLength: PropTypes.number,
  minLength: PropTypes.number,
};

TextFieldRegional.defaultProps = {
  value: "",
  label: "",
  placeholder: "",
  type: "text",
  validators: [],
  labelClass: "col-md-4",
  inputclass: "col-md-6",
  style: {},
  labelStyle: {},
  iconClass: "",
  maxLength: 1000,
  minLength: 0,
};
export default TextFieldRegional;

