import React, { useState, useEffect } from 'react';
import './SearchResult.css'
import ViewDetails from './ViewDetails';
import { Link, useParams } from 'react-router-dom';
import $ from 'jquery';
//Comment Added
//Comment Added
//Comment Added
function SearchResult({ searchedData ,vernacularLanguage,KeyLanguage}) {
    const [searchTerm, setSearchTerm] = useState('')
    const [tableHTMLData, setTableHTMLData] = useState('')
    useEffect(() => {
        getPagination('#table-id');
        setSearchTerm()
        $('#maxRows').trigger('change');
        function getPagination(table) {

            $('#maxRows').on('change', function () {
                $('.pagination').html('');						// reset pagination div
                var trnum = 0;									// reset tr counter 
                var maxRows = parseInt($(this).val());			// get Max Rows from select option

                var totalRows = $(table + ' tbody tr').length;		// numbers of rows 
                $(table + ' tr:gt(0)').each(function () {			// each TR in  table and not the header
                    trnum++;									// Start Counter 
                    if (trnum > maxRows) {						// if tr number gt maxRows

                        $(this).hide();							// fade it out 
                    } if (trnum <= maxRows) { $(this).show(); }// else fade in Important in case if it ..
                });											//  was fade out to fade it in 
                if (totalRows > maxRows) {						// if tr total rows gt max rows option
                    var pagenum = Math.ceil(totalRows / maxRows);	// ceil total(rows/maxrows) to get ..  
                    //	numbers of pages 
                    for (var i = 1; i <= pagenum;) {			// for each page append pagination li 
                        $('.pagination').append('<li data-page="' + i + '">\
								      <span>'+ i++ + '<span class="sr-only">(current)</span></span>\
								    </li>').show();
                    }											// end for i 


                } 												// end if row count > max rows
                $('.pagination li:first-child').addClass('active'); // add active class to the first li 


                //SHOWING ROWS NUMBER OUT OF TOTAL DEFAULT
                showig_rows_count(maxRows, 1, totalRows);
                //SHOWING ROWS NUMBER OUT OF TOTAL DEFAULT

                $('.pagination li').on('click', function (e) {		// on click each page
                    e.preventDefault();
                    var pageNum = $(this).attr('data-page');	// get it's number
                    var trIndex = 0;							// reset tr counter
                    $('.pagination li').removeClass('active');	// remove active class from all li 
                    $(this).addClass('active');					// add active class to the clicked 


                    //SHOWING ROWS NUMBER OUT OF TOTAL
                    showig_rows_count(maxRows, pageNum, totalRows);
                    //SHOWING ROWS NUMBER OUT OF TOTAL



                    $(table + ' tr:gt(0)').each(function () {		// each tr in table not the header
                        trIndex++;								// tr index counter 
                        // if tr index gt maxRows*pageNum or lt maxRows*pageNum-maxRows fade if out
                        if (trIndex > (maxRows * pageNum) || trIndex <= ((maxRows * pageNum) - maxRows)) {
                            $(this).hide();
                        } else { $(this).show(); } 				//else fade in 
                    }); 										// end of for each tr in table
                });										// end of on click pagination list
            });
            // end of on select change 

            // END OF PAGINATION 

        }




        // SI SETTING
        /* $(function () {
            // Just to append id number for each row  
            default_index();

        }); */

        //ROWS SHOWING FUNCTION
        function showig_rows_count(maxRows, pageNum, totalRows) {
            //Default rows showing
            var end_index = maxRows * pageNum;
            var start_index = ((maxRows * pageNum) - maxRows) + parseFloat(1);
            var string = 'Showing ' + start_index + ' to ' + (end_index > totalRows ? totalRows : end_index) + ' of ' + totalRows + ' entries';
            $('.rows_count').html(string);
        }

        // CREATING INDEX
        function default_index() {
            $('table tr:eq(0)').prepend('<th> S. No. </th>')

            var id = 0;

            $('table tr:gt(0)').each(function () {
                id++
                $(this).prepend('<td>' + id + '</td>');
            });
        }

    }, [searchedData])
    function FilterkeyWord_all_table(e) {

        var count = 10;
        var input, filter, table, tr, td, i;
        input = e.target;
        var input_value = e.target.value;
        filter = input.value.toLowerCase();
        if (input_value != '') {
            table = document.getElementById("table-id");
            tr = table.getElementsByTagName("tr");
            for (let i = 1; i < tr.length; i++) {

                var flag = 0;

                for (let j = 0; j < count; j++) {
                    td = tr[i].getElementsByTagName("td")[j];
                    if (td) {

                        var td_text = td.innerHTML;
                        if (td.innerHTML.toLowerCase().indexOf(filter) > -1) {

                            flag = 1;
                            if (flag == 1) {

                            }
                        } else {
                            //DO NOTHING
                        }
                    }
                }
                if (flag == 1) {
                    tr[i].style.display = "";
                } else {
                    tr[i].style.display = "none";
                }
            }
        } else {
            //RESET TABLE
            $('#maxRows').trigger('change');
        }
    }
    return (
        <div className='result-main' id="style-2">

            <h4 align="center" style={{ marginTop: 10, marginBottom: 10 }}>{searchedData?.length === 0 ? "No Result Found" : `Search Result : ${searchedData?.length}`}</h4>
            {searchedData?.length > 0 && <>
                <div className="header_wrap">
                    <div className="num_rows">
                        <div className="form-group" style={{ width: 100 }}>
                            <select className="form-control" name="state" id="maxRows">
                                <option value="5">5</option>
                                <option value="10">10</option>
                                <option value="15">15</option>
                                <option value="20">20</option>
                                <option value="50">50</option>
                                <option value="100">70</option>
                                <option value="5000">Show ALL Rows</option>
                            </select>
                        </div>
                    </div>
                    <div className="tb_search" style={{ width: 200 }}>
                        <input type="text" id="search_input_all" onKeyUp={(e) => FilterkeyWord_all_table(e)} placeholder="Search..." className="form-control"></input>
                    </div>
                </div>
                <table className='result-table' id="table-id">
                    <thead>
                        <tr bgcolor="#CBECFF">
                            <td><b>S. NO.</b></td>
                            <td><b>Epic Number</b></td>
                            <td><b>Name</b></td>
                            <td><b>Age</b></td>
                            <td><b>Relative Name</b></td>
                            <td><b>State</b></td>
                            <td><b>District</b></td>
                            <td><b>Assembly Constituency</b></td>
                            {/* <td><b>Parliamentary Constituency</b></td> */}
                            <td><b>Polling Station</b></td>
                            <td><b>Action</b></td>
                        </tr>
                    </thead>
                    <tbody>
                        {searchedData != [] && (
                            searchedData.map((d, i) => (
                                <tr>
                                    <td>{i + 1}</td>
                                    <td>{d.content.epicNumber != null ? d.content.epicNumber : ""}</td>
                                    <td>{(d.content.applicantFirstName != null ? d.content.applicantFirstName : "") + " " + (d.content.applicantLastName != null ? d.content.applicantLastName : "")}<br/> {(d.content.applicantFirstNameL1 != null ? d.content.applicantFirstNameL1 : "") + " " + (d.content.applicantLastNameL1 != null ? d.content.applicantLastNameL1 : "")}</td>
                                    <td>{d.content.age != null ? d.content.age : ""}</td>
                                    <td>{(d.content.relationName != null ? d.content.relationName : "") + " " + (d.content.relationLName != null ? d.content.relationLName : "")} <br/> {(d.content.relationNameL1 != null ? d.content.relationNameL1 : "") + " " + (d.content.relationLNameL1 != null ? d.content.relationLNameL1 : "")}</td>
                                    <td>{d.content.stateName != null ? d.content.stateName : ""}</td>
                                    <td>{d.content.districtValue != null ? d.content.districtValue : ""}</td>
                                    <td>{d.content.asmblyName != null ? d.content.asmblyName : ""}</td>
                                    {/* <td>{d.content.prlmntName != null ? d.content.prlmntName : ""}</td> */}
                                    <td>{d.content.psbuildingName != null ? d.content.psbuildingName : ""}</td>
                                    <td><Link to={`/viewdetail/${window.btoa(d.content.epicNumber)}/${window.btoa(d.content.id)}/${window.btoa(d.content.stateCd)}/${window.btoa(KeyLanguage)}`} target="_blank">View Details</Link></td>
                                </tr>
                            ))


                        )}

                    </tbody>
                </table>
                <div className='pagination-container' style={{ margin: "0 30px" }}>
                    <nav>
                        <ul className="pagination">
                        </ul>
                    </nav>
                </div>
                <div className="rows_count" style={{ margin: "0 30px" }}>Showing 11 to 20 of 91 entries</div>
            </>}




        </div>
    );
}
export default SearchResult;