import React, { useEffect, useState } from 'react';
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "../../node_modules/react-tabs/style/react-tabs.css";
import './SearchContainer.css'
import SearchByDetails from './SearchByDetails';
import SearchByEPIC from './SearchByEPIC';
import logo from './logoTemp.png';
import tcslogo from './TCS_logo.svg';
import voterSearchBg from './voterSearchBg.png';
import vsp from './vsp.svg'
import SearchLogo from './SearchLogo.svg';
import {  languageLabelHindi as hi, languageLabel, languageLabelAssamese as as, languageLabelBengali as bn, languageLabelKonkani as ko, languageLabelGujrati as gj, languageLabelMalayalam as ml, languageLabelPunjabi as pn, languageLabelTamil as tm, languageLabelTelugu as tl, languageLabelOdia as od } from '../Language/languageLabel';
import SearchByMobile from './SearchByMobile';
import mobile from './mobile.svg'
import details from './details.svg'
import epicCard from './epicCard.svg'
//Phase2 and Phase 3 State language label added


function SearchContainer() {
    //const logo = require('./logoTemp.png');
    const [vernacularLanguage, setVernacularLanguage] = useState(hi)
    const [divSelected, setDivSelected] = useState(1)
    function renderHandler(e) {
        
        if (e == "detail") {
            setDivSelected(1)
        }
        else if (e == "epic") {
            setDivSelected(2)
        }
        else if (e == "mobile") {
            setDivSelected(3)
        }
    }
    useEffect(() => {
        setVernacularLanguage(hi)
    }, [divSelected])
    return (
        <>
            {/* <img src={tcslogo} className="tcs-logo"/> */}
            <div className='search-container-logo'/* style={{ height: 100, width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }} */>
                <div className='search-container-sub-logo'>
                    <img src={vsp} />
                    <div style={{ marginLeft: 20 }}>
                        <span style={{ letterSpacing: '0.03em', fontWeight: 700 }}>मतदाता सेवा पोर्टल</span>
                        <br />
                        <span style={{ letterSpacing: '0.03em', fontWeight: 700 }}>VOTERS' SERVICE PORTAL</span>
                    </div>
                </div>
                <div /* className='search-container-navbar' */ className='search-container-navbar-logo'>
                    <a href="https://voters.eci.gov.in/" target="_blank" style={{ textDecoration: "none", padding: 10, marginRight: 30 }}><span style={{ color: "#2B2B2D", fontWeight: 'bold' }}>Home </span> <svg width="12" height="12" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M16 16H2V2H9V0H2C0.89 0 0 0.9 0 2V16C0 17.1 0.89 18 2 18H16C17.1 18 18 17.1 18 16V9H16V16ZM11 0V2H14.59L4.76 11.83L6.17 13.24L16 3.41V7H18V0H11Z" fill="black" />
                    </svg>
                    </a>
                    <a href="https://nvsp.in/Home/About" target="_blank" style={{ textDecoration: "none", padding: 10, marginRight: 30 }}><span style={{ color: "#2B2B2D", fontWeight: 'bold' }}>About Us </span> <svg width="12" height="12" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M16 16H2V2H9V0H2C0.89 0 0 0.9 0 2V16C0 17.1 0.89 18 2 18H16C17.1 18 18 17.1 18 16V9H16V16ZM11 0V2H14.59L4.76 11.83L6.17 13.24L16 3.41V7H18V0H11Z" fill="black" />
                    </svg>
                    </a>
                    <a href="#" style={{ textDecoration: "none", padding: 10, marginRight: 10 }}><span style={{ color: "#2B2B2D", fontWeight: 'bold' }}>Contact Us </span> <svg width="12" height="12" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M16 16H2V2H9V0H2C0.89 0 0 0.9 0 2V16C0 17.1 0.89 18 2 18H16C17.1 18 18 17.1 18 16V9H16V16ZM11 0V2H14.59L4.76 11.83L6.17 13.24L16 3.41V7H18V0H11Z" fill="black" />
                    </svg>
                    </a>
                </div>
            </div>

            {/* <div style={{ height: 20, width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

            </div> */}

            <div className='search-container-main'>
                <div className='search-container-submain'>
                    <div className='search-container-main-header'>
                        <a style={{ position: 'absolute', fontSize: 14 }} href="#">{vernacularLanguage.applyOnline} <br />/ Apply Online</a>
                        <p className="hiddens" align="center" style={{ fontSize: 24, fontWeight: 500 }}><img src={SearchLogo} height={20} /> Search in Electoral Roll</p>
                        <a style={{ position: 'absolute', right: 70, top: 105, fontSize: 14 }} href="#">{vernacularLanguage.help} / Help</a>
                    </div>
                    <p className="shows" align="center" style={{ fontSize: 20, marginTop: 90 }}>Search in Electoral Roll</p>
                    <div style={{ display: 'flex', marginBottom: 20 }}>
                        <div id="detail" style={{ padding: "5px 0px", width: '50%', alignContent: 'center', fontWeight: divSelected == 1 ? 800 : 500 }} onClick={() => renderHandler('detail')}><p align="center" className={divSelected == 1 ? "container-tab-active" : "container-tab-inactive"}  ><img src={details} /> &nbsp;{vernacularLanguage.details}/ Search by Details</p>{/* <div className={divSelected == 1 ? 'tab-design-active' : 'tab-design-inactive'}></div> */}</div>
                        <div id="epic" style={{ padding: "5px 0px", width: '50%', alignContent: 'center', fontWeight: divSelected == 2 ? 800 : 500 }} onClick={(e) => renderHandler('epic')}><p align="center" className={divSelected == 2 ? "container-tab-active" : "container-tab-inactive"}  ><img src={epicCard} /> &nbsp;{vernacularLanguage.byEpic} / Search by EPIC </p>{/* <div className={divSelected == 2 ? 'tab-design-active' : 'tab-design-inactive'}></div> */}</div>
                        <div id="mobile" style={{ padding: "5px 0px", width: '50%', alignContent: 'center', fontWeight: divSelected == 3 ? 800 : 500 }} onClick={(e) => renderHandler('mobile')}><p align="center" className={divSelected == 3 ? "container-tab-active" : "container-tab-inactive"}><img src={mobile} /> &nbsp;{vernacularLanguage.mobileno} / Search by Mobile </p>{/* <div className={divSelected == 3 ? 'tab-design-active' : 'tab-design-inactive'}></div> */}</div>
                    </div>
                    {divSelected == 1 && <SearchByDetails vernacularLanguage={vernacularLanguage} setVernacularLanguage={setVernacularLanguage}></SearchByDetails>}
                    {divSelected == 2 && <SearchByEPIC vernacularLanguage={vernacularLanguage} setVernacularLanguage={setVernacularLanguage}></SearchByEPIC>}
                    {divSelected == 3 && <SearchByMobile vernacularLanguage={vernacularLanguage} setVernacularLanguage={setVernacularLanguage}></SearchByMobile>}

                </div>
            </div>
        </>

    )
}
export default SearchContainer;