import React,{useState} from "react";
import { AccordionCollapse } from "react-bootstrap";
import AppHistoryTrack from "../../../Search/AppHistoryTrack";

const TableRowAppHistory = ({ data, stat, id, status }) => {
  const [open, setOpen] = useState(false);

 return (
    <>
      <tr className="border rounded-lg" id={id}>
        {data.map((item) => {
          return <td>{item}</td>;
        })}
        <td>
          <b>
            <i
              className="pe-7s-angle-down pe-3x pe-va"
              onClick={() => setOpen(!open)}
            ></i>
          </b>
        </td>
        {/*<IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
            */}
      </tr>
      { open && (
      <tr className="border border-top-0">
        <td colSpan="12">
          <AccordionCollapse in={open} unmountOnExit>
            <AppHistoryTrack
              class1={stat[0]}
              class2={stat[1]}
              class3={stat[2]}
              class4={stat[3]}
              class5={stat[4]}
              class6={stat[5]}
              class7={stat[6]}
              class8={status[0]}
            />
          </AccordionCollapse>
        </td>
      </tr>
      )}
    </>
  );
};

export default TableRowAppHistory;
