import React, { useState } from "react";
import PropTypes from "prop-types";
import { validateInput } from "../../utilities/Validator";
import "../../css/common.css";
const DateInput = ({
  max,
  min,
  onChange,
  value,
  label,
  style,
  id,
  labelHindi,
  labelEnglish,
  labelStyle,
  inputClass,
  required,
  labelClass,
  disabled,
  validators,
  getError,
  name,
  Error,
  dateClass,
  type,
  onChangeRadio,
  radioValue,
  radioChecked,
  errorStatus,
  errorLabels,
  errorLabel,
  setErrorStatus
}) => {
  // const getMax = () => {
  //   return new Date().toISOString().substring(0, 10);
  // };
  // const getMin = () => {
  //   return new Date().toISOString().substring(0, 10);
  // }
  const [error, setError] = useState(false);
  const handleChange = (event) => {
    const { value } = event.target;
    if (validateInput(validators, value))
      setError(validateInput(validators, value));
    else if (
      // type === "email" &&
      !validateInput(validators, value) &&
      value !== ""
    ) {
      setError(false);
    } else {
      setError(false);
    }
    onChange(value);
    getError &&
      getError(
        validateInput(validators, value)
          ? validateInput(validators, value)
          : false
      );
    if (value != '' && value != 'NA') {
      if (errorStatus.hasOwnProperty(errorLabel)) {
        setErrorStatus(prev => ({ ...prev, [errorLabel]: false }))
      }
    }
  };
  const handleChangeRadio = (val) => {
    setError(validateInput(validators, val));
    onChangeRadio(val);
    getError &&
      getError(
        validateInput(validators, val) ? validateInput(validators, val) : false
      );

  };


  return (
    <>
      <div className={labelClass}>
        <input
          type="radio"
          name={name}
          value={radioValue}
          onChange={(e) => handleChangeRadio(e.target.value)}
          checked={radioChecked}
          className={inputClass}
          id={id}
          //disabled={disabled}
          style={{ transform: 'scale(1.5)', marginRight: 10 }}
        />
        <label className="select-label" style={labelStyle ? labelStyle : {}}>
          <span style={{ fontWeight: 500 }}>{labelHindi}</span>/<br /><span style={{ fontWeight: 700 }}> {labelEnglish}</span>
          {required ? <b className={"text-danger"}> *</b> : ""}
        </label>
      </div>
      <div className={inputClass}>
        <input
          type={type}
          name={name}
          max={max}
          value={value}
          onChange={handleChange}
          min={min}
          style={style}
          className={`form-control library-date ${dateClass}`}
          placeholder="Date"
          disabled={disabled}
          // disableEntry={true}
          onKeyDown={(e) => {
            e.preventDefault();
          }}
        />
        {errorLabel && errorStatus[errorLabel] && <span className={`text-danger errormsg`}>* {errorLabels[errorLabel]}</span>}
      </div>
    </>
  );
};

DateInput.prototype = {
  value: PropTypes.string,
  label: PropTypes.string,
  placeholder: PropTypes.string,
  validators: PropTypes.array,
  type: PropTypes.string,
  onchange: PropTypes.func.isRequired,
  labelClass: PropTypes.string,
  inputClass: PropTypes.string,
};

DateInput.defaultProps = {
  value: "",
  label: "Date",
  placeholder: "",
  type: "date",
  labelClass: "col-md-4",
  inputClass: "col-md-6",
  name: "date"
};
export default DateInput;
