import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { validateInput } from "../../utilities/Validator";
import "../../css/common.css";
const InputField = ({
  value,
  id,
  label,
  labelHindi,
  labelEnglish,
  placeholder,
  name,
  required,
  validators,
  type,
  onChange,
  labelClass,
  inputClass,
  labelStyle,
  style,
  disabled,
  spanTitle,
  onKeyPress,
  tabIndex,
  maxLength,
  onKeyUp,
  iconclass,
  getError,
  minLength,
  Error,
  errorStatus,
  errorLabels,
  errorLabel,
  setErrorStatus,
}) => {
  const [error, setError] = useState(false);
  const [success, setSuccess] = useState(false);
  useEffect(() => {}, []);
  const handleChange = (event) => {
    const { value } = event.target;
    if (validateInput(validators, value))
      setError(validateInput(validators, value));
    else if (
      type === "email" &&
      !validateInput(validators, value) &&
      value !== ""
    ) {
      setSuccess(true);
      setError(false);
    } else {
      setError(false);
    }
    onChange(value);
    /* if(id=="epicID"){
      onChange(value.toUpperCase());
      
    }
    else{
      
    } */

    getError &&
      getError(
        validateInput(validators, value)
          ? validateInput(validators, value)
          : false
      );
    if (value != "") {
      if (errorStatus.hasOwnProperty(errorLabel)) {
        setErrorStatus((prev) => ({ ...prev, [errorLabel]: false }));
      }
    }
  };

  return (
    <>
      {label && (
        <div className={labelClass}>
          <label className="input-label" style={labelStyle}>
            <span style={{ fontWeight: 500 }}>{labelHindi}</span>/<br />
            <span style={{ fontWeight: 500 }}>{labelEnglish}</span>
            {required ? <b className={"text-danger"}> *</b> : ""}
          </label>
        </div>
      )}

      <div className={inputClass}>
        <div className="form-group">
          <input
            type={type}
            value={value}
            name={name}
            // className="form-control"
            placeholder={placeholder}
            onChange={handleChange}
            style={style}
            disabled={disabled}
            onKeyPress={onKeyPress}
            tabIndex={tabIndex}
            onKeyUp={onKeyUp}
            maxLength={maxLength}
            minLength={minLength}
            iconclass={iconclass}
            id={id}
            className={
              error
                ? "library-input form-control error"
                : success
                ? "library-input form-control success"
                : "library-input form-control"
            }
          ></input>
          {iconclass && <i className={iconclass}></i>}
          {spanTitle && <small>{spanTitle}</small>}
          {/* {(error || (Error && Error[name])) && (
            <span className="text-danger errormsg">
              {error.message || Error[name]}
            </span>
          )} */}
          {errorLabel && errorStatus[errorLabel] && <span className={`text-danger errormsg`}>* {errorLabels[errorLabel]}</span>}
        </div>
      </div>
    </>
  );
};

InputField.prototype = {
  value: PropTypes.string,
  label: PropTypes.string,
  placeholder: PropTypes.string,
  validators: PropTypes.array,
  type: PropTypes.string,
  onchange: PropTypes.func.isRequired,
  labelClass: PropTypes.string,
  inputClass: PropTypes.string,
  style: PropTypes.object,
  labelStyle: PropTypes.object,
  iconclass: PropTypes.string,
  maxLength: PropTypes.number,
  minLength: PropTypes.number,
};

InputField.defaultProps = {
  value: "",
  label: "",
  placeholder: "",
  type: "text",
  validators: [],
  labelClass: "col-md-4",
  inputclass: "col-md-6",
  style: {},
  labelStyle: {},
  iconClass: "",
  maxLength: 1000,
  minLength: 0,
};
export default InputField;
