import React, { useState, useEffect } from "react";
import { Validators } from "../library/utilities/Validator";
import "./SearchByDetails.css";
import InputField from "../library/components/InputFieldVernacular";
import SelectField from "../library/components/Select";
import axios from "axios";
import SearchResult from "../SearchResult/SearchResult";
import ReactToPrint from "react-to-print";
import CaptchaPage from "../captcha/CaptchaPage";
import SearchLogo from "./search-svg.svg";
import { languageLabelEnglish } from "../Language/languageLabel";
import { languageLabelHindi as hi, languageLabel, languageLabelAssamese as as, languageLabelBengali as bn, languageLabelKonkani as ko, languageLabelGujrati as gj, languageLabelMalayalam as ml, languageLabelPunjabi as pn, languageLabelTamil as tm, languageLabelTelugu as tl, languageLabelOdia as od } from '../Language/languageLabel';
import SpinnerWithButton from "../library/components/SpinnersWithButton/index";
//capatalize Added
function SearchByEPIC({ vernacularLanguage, setVernacularLanguage }) {
  let errorState = {
    epic: false,
    state: false,
  };
  let errorLabels = {
    epic: "Epic Number is Required",
    state: "State is Required",
  };
  let languageCodeMapping = {
    assamese: as,
    bengali: bn,
    hindi: hi,
    konkani: ko,
    gujarati: gj,
    malayalam: ml,
    punjabi: pn,
    tamil: tm,
    odia: od,
    telugu: tl,
  };
  let KeyboardDictionaryLanguage = [
    {
      languageName: "Select Language",
      languageCode: "NA",
    },
    {
      languageName: "Hindi/हिंदी",
      languageCode: "hindi",
    },
    {
      languageName: "Assamese/অসমীয়া",
      languageCode: "assamese",
    },
    {
      languageName: "Bengali/বাংলা",
      languageCode: "bengali",
    },
    {
      languageName: "Konkani/कोंकणी",
      languageCode: "konkani",
    },
    {
        languageName: "Gujarati/ગુજરાતી",
        languageCode: "gujarati"
    },
    {
        languageName: "Malayalam/മലയാളം",
        languageCode: "malayalam"
    },
    {
        languageName: "Odia/ଓଡିଆ",
        languageCode: "odia"
    },
    {
        languageName: "Punjabi/ਪੰਜਾਬੀ",
        languageCode: "punjabi"
    },
    {
        languageName: "Tamil/தமிழ்",
        languageCode: "tamil"
    },
    {
        languageName: "Telugu/తెలుగు",
        languageCode: "telugu"
    }

  ];

  const [epic, setEpic] = useState("");
  const [states, setStates] = useState([
    { stateCode: "NA", stateName: "Select Your State" },
  ]);
  const [selectedState, setSelectedState] = useState("NA");
  const [selectedStateName, setSelectedStateName] = useState("");
  const [resultData, setResultData] = useState([]);
  const [isDisabled, setIsDisabled] = useState(true);
  const [showResultComponent, setShowResultComponent] = useState(false);
  const [errorValue, setErrorValue] = useState({});
  const [errorStatus, setErrorStatus] = useState(errorState);
  const [captchaData, setCaptchaData] = useState("NA");
  const [captchaArg, setCaptchaArg] = useState(true);
  const [captchaId, setCaptchaId] = useState("");
  const [errorMsg, setErrorMsg] = useState("");
  const [successMsg, setSuccessMsg] = useState("");
  const [captchaStatus, setCaptchaStatus] = useState(400);
  const [KeyLanguage, setKeyLanguage] = useState("NA");
  const [KeyboardLanguage, setKeyboardLanguage] = useState(
    KeyboardDictionaryLanguage
  );
  let token =
    "eyJhbGciOiJSUzI1NiIsInR5cCIgOiAiSldUIiwia2lkIiA6ICJYZXlIVWo0MTdmRUh2MDBZUHVRa3VRd2RGRXk5Z2JtU0NnSXRsZmNiNjdBIn0..VXCKGtHjWRj2io3ePmarCdVHfEzVpmuY_Ok_VYeITN_iu2fp32xLZ9K-9RWSsByi2bIxm8XmyhMRznOCqKDCLgcaJ-Gp5-PngY35IGKMZ4BFZSlpge5dBr3vvWuoOLBJvZMHg90NC5cRZI8aKz2mFqn_PKnVylD-woMGM01ipy4LKzcTLe3h9Ued8900kFRS_fZ9jbVJhYBFND8P8uL-BxERCbAqTvPcamHcL_hbjxXbkoORqolLA3M-N6Y1SSEiBSVUNrghbR6FUL6f45hyhtiB4BfsoY2Zo9WtZrp25GCaz-HoPLvQTgeAQ2_6IMs3X_cxcRZ8WB_NDrrv5bLjWw";

  const getErrorValue = (key, value) => {
    setErrorValue((prevState) => ({
      ...prevState,
      [key]: value,
    }));
  };
  function clearHandler() {
    setEpic("");
    setErrorValue({});
    setSelectedState("NA");
    setSelectedStateName("");
    setResultData([]);
    setIsDisabled(true);
    setShowResultComponent(false);
    setCaptchaStatus(400);
    setCaptchaArg((prev) => !prev);
    setCaptchaId("");
    setErrorMsg("");
    setSuccessMsg("");
    setKeyLanguage("NA")
  }
  function changeStateHandler(value, labelName) {
    setSelectedState(value);
    setSelectedStateName(labelName);
  }
  async function submitHandler() {
    setShowResultComponent(false);
    setResultData([]);
    let personData = {isPortal:true};
    if (epic != "") {
      setErrorStatus((prev) => ({ ...prev, epic: false }));

      if (errorValue["epicNumber"] == false) {
        personData["epicNumber"] = epic;
      } else {
        return;
      }
    } else {
      setErrorStatus((prev) => ({ ...prev, epic: true }));
      return;
    }
    if (selectedState != "NA") {
      personData["stateCd"] = selectedState;
      setErrorStatus((prev) => ({ ...prev, state: false }));
    }

    if (captchaStatus != 200) {
      window.alert("Please Verify Captcha");
      return;
    }
    setResultData("LD");
    axios
      .post(
        process.env.REACT_APP_ELECTORAL_SEARCH_BASE_URL +
        "api/v1/elastic/search-by-epic-from-national-display",
        personData
      )
      .then((response) => {
        setResultData(response.data);
        setShowResultComponent(true);
      });
  }
  function verifyCaptchaHandler() {
    if (captchaId == "") {
      window.alert("Please Enter the Captcha")
    }
    axios
      .post(
        process.env.REACT_APP_CAPTCHA_BASE_URL +
        `api/v1/captcha-service/verifyCaptcha/${captchaId}?id=${captchaData?.id}`
      )
      .then((response) => {
        setCaptchaStatus(response.data.statusCode);
        if (response.data.statusCode == 400) {
          setErrorMsg("Wrong Captcha");
        } else if (response.data.statusCode == 200) {
          setSuccessMsg("Captcha Verified");
        }
      });
  }
  useEffect(() => {
    axios
      .get(
        process.env.REACT_APP_CAPTCHA_BASE_URL +
        "api/v1/captcha-service/generateCaptcha"
      )
      .then((response) => {
        if (response) setCaptchaData(response.data);
      });
  }, [captchaArg]);
  useEffect(() => {
    document
      .getElementById("style-2")
      ?.scrollIntoView({
        behavior: "smooth",
        block: "center",
        inline: "nearest",
      });
  }, [resultData]);
  useEffect(() => {
    axios
      .get(
        process.env.REACT_APP_COMMON_SERVICES_BASE_URL + "api/v1/common/states"
      )
      .then((response) => {
        response.data = response.data.filter(d => ['S03', 'S04', 'S05', 'S28', 'S06', 'S07', 'S08', 'S11', 'S12', 'S16', 'S18', 'S19', 'S20', 'S21', 'S22', 'S26', 'S27', 'S01', 'S29', 'U01', 'U02', 'U03', 'U05', 'U07', 'S02', 'S14'].includes(d.stateCd))
        response.data.unshift({ stateCd: "NA", stateName: "Select Your State" });
        setStates(response.data)
      });
  }, []);

  useEffect(() => {
    if (resultData.length > 0 || captchaStatus == 200) {
      setResultData([])
      setShowResultComponent(false)
      setCaptchaId("")
      setErrorMsg("")
      setSuccessMsg("")
      setCaptchaStatus(400)
      setCaptchaArg(prev => !prev)
    }
  }, [epic, selectedState, captchaId])
  //console.log(states, selectedState, selectedStateName)
  return (
    <>
      {resultData == "LD" && (
        <div className="globalSpinnerDiv">
          <SpinnerWithButton spinnerClass="globalSpinner" />
        </div>
      )}
      {resultData != "LD" && (
        <div
          className="search-details-main"
          style={{ backgroundColor: "rgba(0,0,0,0)" }}
        >
          <div
            style={{
              marginRight: 30,
              width: "100%",
              display: "flex",
              justifyContent: "center",
              flexWrap: "wrap",
            }}
          >
            <div className="search-label">
              <SelectField
                id="KeyboardID"
                labelEnglish={languageLabelEnglish.keyboard}
                labelHindi={vernacularLanguage.keyboard}
                labelStyle={{
                  whiteSpace: "wrap",
                  fontWeight: 500,
                  marginRight: 20,
                }}
                options={KeyboardLanguage}
                valueKey="languageCode"
                labelKey="languageName"
                style={{ width: 240, height: 40 }}
                inputClass="noclass"
                handleChange={(value, labelName) => {
                  setKeyLanguage(value);
                  setKeyLanguage(value);
                  value == "NA"
                    ? setVernacularLanguage(hi)
                    : setVernacularLanguage(languageCodeMapping[value]);
                }}
                value={KeyLanguage}
                labelClass="noclass"
                errorStatus={errorStatus}
                errorLabels={errorLabels}
                errorLabel="keyboard"
                setErrorStatus={setErrorStatus}
                labelBreak={false}
              />
            </div>
          </div>
          <div
            className="search-details-container"
            style={{
              marginTop: 20,
              marginBottom: 20,
              backgroundColor: "rgba(0,0,0,0)",
            }}
          >
            <div>
              <p
                style={{
                  display: "inline-block",
                  position: "relative",
                  bottom: 13,
                  left: 10,
                  backgroundColor: "white",
                  fontSize: 16,
                  fontWeight: 800,
                  margin: 0,
                  color: "#0065EB",
                }}
              >
                EPIC DETAIL
              </p>
            </div>
            <div
              className="left-pane-body"
              style={{ backgroundColor: "rgba(0,0,0,0)" }}
            >
              <div className="search-details-subbody-epic">
                <div className="abc">
                  <InputField
                    id="epicID"
                    label={true}
                    labelEnglish={languageLabelEnglish.epic}
                    labelHindi={vernacularLanguage.epic}
                    placeholder="Your EPIC Number"
                    style={{ width: 240, height: 40 }}
                    labelStyle={{ whiteSpace: "nowrap", fontWeight: 500 }}
                    value={epic}
                    inputClass="noclass"
                    labelClass="noclass"
                    onChange={(value) => {
                      setEpic(value.toUpperCase());
                    }}
                    validators={[]}
                    required={true}
                    getError={(value) => getErrorValue("epicNumber", value)}
                    errorStatus={errorStatus}
                    errorLabels={errorLabels}
                    errorLabel="epic"
                    setErrorStatus={setErrorStatus}
                  >
                    {" "}
                  </InputField>
                </div>
                <div className="abc">
                  <SelectField
                    labelEnglish={languageLabelEnglish.state}
                    labelHindi={vernacularLanguage.state}
                    options={states}
                    valueKey="stateCd"
                    labelKey="stateName"
                    style={{ width: 240, height: 40 }}
                    labelStyle={{ whiteSpace: "nowrap", fontWeight: 500 }}
                    handleChange={(value, labelName) =>
                      changeStateHandler(value, labelName)
                    }
                    value={selectedState}
                    /* errorStatus={errorStatus} errorLabels={errorLabels} errorLabel="state" setErrorStatus={setErrorStatus} */ labelClass="noclass"
                    inputClass="noclass"
                  ></SelectField>
                </div>
              </div>
              <div></div>
            </div>
          </div>
          <div className="search-buttons">
            <div>
              <CaptchaPage
                captchaText="Enter Captcha"
                captchaData={captchaData}
                captchaArg={captchaArg}
                setCaptchaArg={setCaptchaArg}
                setCaptchaId={setCaptchaId}
                captchaId={captchaId}
                error={errorMsg}
                success={successMsg}
                setErrorMsg={setErrorMsg}
                setSuccessMsg={setSuccessMsg}
                verifyCaptchaHandler={verifyCaptchaHandler}
                captchaStatus={captchaStatus}
                setCaptchaStatus={setCaptchaStatus}
              />
            </div>
            <div style={{ display: "flex", justifyContent: "center", gap: 30 }}>
              <button
                className={true ? "btn-active" : "btn-inactive"}
                onClick={submitHandler}
                disabled={false}
              >
                {" "}
                SEARCH{" "}
              </button>
              <button className="btn-active" onClick={clearHandler}>
                {" "}
                CLEAR{" "}
              </button>
            </div>
          </div>
        </div>
      )}
      {showResultComponent && (
        <SearchResult
          searchedData={resultData}
          KeyLanguage={KeyLanguage}
        ></SearchResult>
      )}
    </>
  );
}
export default SearchByEPIC;
